<template>
  <div>
    <div class="row">
      <div class="col-3">
        <h3 class="fw-bold">Category</h3>
      </div>
      <div class="col-6">
        <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details>
        </v-text-field>
      </div>
      <div class="col">
        <router-link to="/admin/maincategories/new">
          <v-btn tile color="error" large>
            <v-icon left>mdi-plus</v-icon>CREATE CAREGORY</v-btn>
        </router-link>
      </div>
    </div>
    <div class="my-5">
      <v-data-table :search="search" :headers="headers" :items="category" sort-by="calories"
                    :single-select="singleSelect" show-select class="elevation-1" v-model="checkedIds">
        <template v-slot:[`item.cat_img_url`]="{ item }">
          <img :src=(url+item.cat_img_url) style="width: 50px; height: 50px"/>
        </template>
        <template v-slot:top>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>
          <v-dialog v-model="dialog" max-width="700px">
            <v-card>
              <v-card-title class="text-h5 grey lighten-2" style="justify-content:space-between;">{{ formTitle }}
                  <v-icon  @click="close" class="text-h5">mdi-close</v-icon>
              </v-card-title>
              <v-card-text>
                  <v-row>
                    <v-col cols="12" sm="6" md="6">
                      <v-text-field v-model="editedItem.cat_name"
                                    label="Category Name"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="6">
                      <v-file-input accept="image/*" label="Category Image" prepend-icon="mdi-camera" multiple
                                    small-chips v-model="files"
                                    @change="addFiles" v-on:click="isHidden = true"></v-file-input>
                      <v-row>
                        <v-col sm="4" v-for="(file, f) in files" :key="f">
                          <img :ref="'file'" class="img-fluid" width="100"
                               height="100" style="display:block">
                        </v-col>
                        <div v-if="!isHidden">
                          <img :src=(url+editedItem.cat_img_url) style="width: 100px;">
                        </div>

                      </v-row>
                    </v-col>
                    <!-- <v-col cols="12" sm="6" md="6">
                      <v-select
                          v-model="editedItem.cat_feature"
                          :items="features"
                          item-text="text"
                          item-value="id"
                          label="Category Feature"></v-select>
                    </v-col> -->
                    <!-- <v-col cols="12" sm="6" md="6">
                      <v-text-field type="number" v-model="editedItem.cat_sort" label="Category Sort"></v-text-field>
                    </v-col> -->
                    <v-col cols="12" md="12" class="py-0 d-flex" style="justify-content: space-between;">
                      <div>
                        <v-checkbox v-model="editedItem.cat_status" label="Active" value="1"></v-checkbox>
                      </div>
                      <div class="ms-3 pb-3">
                        <v-btn v-if="isLoading" class="my-2 me-3 cred">Loading...</v-btn>
                        <v-btn v-else @click="updateCategory" class="my-4 me-3 cred">UPDATE</v-btn>
                        <v-btn class="my-4 me-3 cred" @click="close">CANCEL</v-btn>
                      </div>
                    </v-col>
                  </v-row>
              </v-card-text>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn class="cred" @click="closeDelete">Cancel</v-btn>
                <v-btn v-if="isLoading" class="my-2 me-3 cred">Loading...</v-btn>
                <v-btn v-else class="cred" @click="deleteItemConfirm">OK</v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="mainCategoryMultidelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn class="cred" @click="closeDelete">Cancel</v-btn>
                <v-btn v-if="isLoading" class="my-2 me-3 cred">Loading...</v-btn>
                <v-btn v-else class="cred" @click="deleteMultipleCategories">OK</v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </template>
        <!-- <template v-slot:[`item.cat_feature`]="{ item }">
            <div v-if="item.cat_feature ==0 ">Normal</div>
            <div v-else-if="item.cat_feature == 1">Featured</div>
            <div v-else-if="item.cat_feature == 2">Top add</div>
        </template> -->
        <template v-slot:[`item.action`]="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)">mdi-pencil</v-icon>
          <v-icon small @click="deleteItem(item)">mdi-delete</v-icon>
        </template>
        <template v-slot:[`item.cat_status`]="{ item }">
          <div v-if="item.cat_status === '1'">
            <v-icon small class="mr-2">mdi-check</v-icon>
          </div>
          <div v-else>
            <v-icon small class="mr-2">mdi-close</v-icon>
          </div>
        </template>
        <template v-slot:footer>
          <div class="text-muted p-3">
            <v-btn color="grey" @click="getSelectedIds">DELETE SELECTED</v-btn>
          </div>
        </template>
      </v-data-table>
      <v-snackbar v-model="snackbar" :timeout="timeout">{{ text }}
        <template v-slot:action="{}">
          <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">Close</v-btn>
        </template>
      </v-snackbar>
    </div>
  </div>
</template>
<script>
import axios from 'axios';
export default {
    name: "MainCategories",
    components: {  },
    data: () => ({
        url:'https://bizlx.itechvision.in',
        search: '',
        files: [],
        readers: [],
        dialog: false,
        dialogDelete: false,
        headers: [
            { text: 'Image', value: 'cat_img_url'},
            { text: 'Title', value: 'cat_name', },
            // { text: 'features', value: 'cat_feature', },
            // { text: 'Sorting', value: 'cat_sort' },
            { text: 'Active', align: 'end', value: 'cat_status', sortable: false },
            { text: 'Actions', align: 'end', value: 'action', sortable: false },
        ],
        items: '',
        mainCategoryMultidelete: false,
        checkedIds: [],
        category: [],
        features: [
          {'id':'0', 'text':"Normal"}, 
          {'id':'1', 'text':"Feature"},
          {'id':'2', 'text':"Top add"},
        ],
        editedIndex: -1,
        editedItem: {
            cat_name: '',
            cat_img_url: '',
            cat_slug: '',
            cat_feature: '0',
            cat_sort: '1',
            cat_status: ''
        },
        defaultItem: {
            cat_name: '',
            cat_img_url: '',
            cat_slug: '',
            // cat_feature: '',
            // cat_sort: '',
            cat_status: ''
        },
        snackbar: false,
        text: '',
        timeout: '',
        isLoading: false,
        isHidden: false
    }),
    computed: {
        formTitle() {
            return this.editedIndex === -1 ? 'New Item' : 'Edit category'
        },
    },
    watch: {
        dialog(val) {
            val || this.close()
        },
        dialogDelete(val) {
            val || this.closeDelete()
        },
        mainCategoryMultidelete(val) {
            val || this.closeDelete();
        },
    },
    mounted() {
        this.getcategory();
    },
    methods: {
        addFiles(){
            this.files.forEach((file, f) => {
                this.readers[f] = new FileReader();
                this.readers[f].onloadend = () => {
                    let fileData = this.readers[f].result
                    let imgRef = this.$refs.file[f]
                    imgRef.src = fileData
                }
                if(this.files[f]){
                    this.readers[f].readAsDataURL(this.files[f]);
                }
            });
        },
        getcategory() { // all cat get
            axios.get("category")
                .then((resp) => {
                    this.category = resp.data.categories
                }
            );
        },
        editItem(item) {
            this.editedIndex = this.category.indexOf(item)
            this.editedItem = Object.assign({}, item)
            this.dialog = true;
        },
        deleteItem(item) {
            this.editedIndex = this.category.indexOf(item)
            this.editedItem = Object.assign({}, item)
            this.dialogDelete = true
        },
        getSelectedIds() { // multiple delete modal open
          var deletedID = {
            ids:this.checkedIds.map(item => item.id)
          };
          if(deletedID.ids.length>0){
            this.mainCategoryMultidelete = true;
          }else{
            this.snackbar = true;
            this.text = "Please select category.";
            this.timeout = 3000;
          }
        },
        deleteMultipleCategories() { // multiple delete here
          this.isLoading = true;
          var deletedID = {
            ids:this.checkedIds.map(item => item.id)
          }; 
            axios.post('admin/multiple/category/delete',deletedID)
              .then((resp) => {
                this.isLoading = false;
                this.mainCategoryMultidelete = false;
                  this.snackbar = true;
                  this.text = resp.data.message;
                  this.timeout = 3000;
                this.getcategory();
            });
        },
        deleteItemConfirm() { // for delete
            this.isLoading = true;
            axios.delete("category/delete?id="+this.editedItem.id)
                .then((resp) => {
                  this.closeDelete();
                    this.isLoading = false;
                    this.snackbar = true;
                    this.text = resp.data.message;
                    this.timeout = 3000;
                }
            );
            this.category.splice(this.editedIndex, 1)
        },
        close() {
            this.dialog = false
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem)
                this.editedIndex = -1
            })
        },
        closeDelete() {
            this.dialogDelete = false;
            this.mainCategoryMultidelete = false;
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem)
                this.editedIndex = -1
            })
        },
        updateCategory() { // for update
            this.isLoading = true;
            const config = {
                headers: { 'content-type': 'multipart/form-data' }
            }
            let data = { 
                cat_id: this.editedItem.id,
                cat_name: this.editedItem.cat_name,
                cat_img_url: this.files[0],
                cat_feature: this.editedItem.cat_feature,
                cat_sort: this.editedItem.cat_sort,
                cat_status: this.editedItem.cat_status
            };
            if (this.editedIndex > -1) {
                axios.post("category/edit/"+this.editedItem.id, data, config)
                .then((resp) => {
                    this.isLoading = false;
                    this.close();
                    this.snackbar = true;
                    this.text = resp.data.message;
                    this.timeout = 2000;
                    this.getcategory();
                });
            } else {
                this.category.push(this.editedItem);
            }
        },
    },
}
</script>